	  	retriablefetch(address,sto,it)//fetching everything about today price of a specific stock, retrying if API gave empty result, cos it is limited to 5 times per minute
		{
		fetch(address).then(function(response){if (response.status!==200){return}
			response.json().then(function(data)
			{
				
				const data1=data[Object.keys(data)[1]];//console.log(data1)
				const data2=data1 ? data1[Object.keys(data1)[0]] :false
				const data3=data2 ? data2[Object.keys(data2)[0]] :false
				
				if (data3)
					console.log(sto.Name + " : "+ data3)//some fetches give only empty data because API imits fetching to only 5 times per minute, I log only successful ones
				if(data3)
					{
					sto.value=data3
					sto.total=sto.value*sto.Quantity
						//console.log(data3)
						//console.log(it.state.portfolios)

						const ports=//set that stock value and total in every portfolio
						it.state.portfolios.map(
							(p)=>
								{p.stocks=p.stocks.map((ps)=>
									{if(ps.Name==sto.Name)						
									{ps.value=sto.value;ps.total=sto.value*ps.Quantity}
									else {}
									return ps
									});
								return p
								}
							)
						it.setState(state => ({
						    portfolios:[...ports]
						  }));localStorage.setItem('state', JSON.stringify(it.state));
						//console.log(ports)
					

		
					}
					
					else//retry in 0.5 seconds
						setTimeout(it.retriablefetch(address,sto,it),500)


				}
			)}
			)
		}


