sortstocks(id)
{
return ((bywhat)=>
{

return (()=>{



const p=JSON.parse(JSON.stringify(Object.assign({}, this.state.portfolios.filter((p)=>p.id==id)[0])));
const initialorder=JSON.parse(JSON.stringify(p.stocks))

if (bywhat=="Name")
p.stocks=p.stocks.sort( function
compare(a, b) {
   return a.Name.localeCompare(b.Name)
}
)

if (bywhat=="Quantity")
p.stocks=p.stocks.sort(function compare(a, b) {
  if (a.Quantity < b.Quantity) {
    return -1;
  }
  if (a.Quantity > b.Quantity) {
    return 1;
  }
  // a must be equal to b
  return 0;
})

if (bywhat=="value")
p.stocks=p.stocks.sort( function compare(a, b) {
  if (a.value < b.value) {
    return -1;
  }
  if (a.value > b.value) {
    return 1;
  }
  // a must be equal to b
  return 0;
})

if (bywhat=="total")
p.stocks=p.stocks.sort(function compare(a, b) {
  if (a.total < b.total) {
    return -1;
  }
  if (a.total > b.total) {
    return 1;
  }
  // a must be equal to b
  return 0;
})/**/



//if sorted order is completely same as existing - then use reversed

const reversedstocks = JSON.parse(JSON.stringify([...p.stocks].reverse()))
//console.log(reversedstocks)
//console.log(p.stocks)
//console.log(initialorder)
/*
console.log()
console.log(initialorder.map((p)=>p.Name).toString()) 
console.log(reversedstocks.map((p)=>p.Name).toString())
console.log(initialorder.map((p)=>p.Name).toString()==reversedstocks.map((p)=>p.Name).toString())
*/

/**/
if(initialorder.map((p)=>p.Name).toString()==reversedstocks.map((p)=>p.Name).toString())
p.stocks=p.stocks

/*
console.log(initialorder.map((p)=>p.Name).toString()) 
console.log(p.stocks.map((p)=>p.Name).toString())
console.log(initialorder.map((p)=>p.Name).toString()==p.stocks.map((p)=>p.Name).toString())
*/

if(initialorder.map((p)=>p.Name).toString()==p.stocks.map((p)=>p.Name).toString())
p.stocks=reversedstocks
//console.log("p.stocks")
//console.log(p.stocks)
/**/
/*
*/

const theportfoliotoedit=p
const ports=this.state.portfolios

ports[this.state.portfolios.map((portfolio,index)=>{if (portfolio.id==theportfoliotoedit.id) return index}).filter((p)=>typeof p !== 'undefined')[0]]=theportfoliotoedit//only in one portfolio the sorting happens

this.setState(state => ({
	    portfolios:[...ports]
	  }));localStorage.setItem('state', JSON.stringify(this.state));


})

}
)
}


