	{event.preventDefault();
	return;}
	

const stockname=this.input2.current.value
const quantity=parseInt(this.input3.current.value.match(/\d/g).join(''), 10)
const date=this.input6.current.value
const theportfoliotoedit=this.state.portfolios.filter((p)=>p.id==this.state.stockbeingaddedto)[0]
theportfoliotoedit.stocks=[...theportfoliotoedit.stocks,{Name:stockname,Quantity:quantity,value:0,total:0,date:date}]//adding stocks to portfolio under question


const ports=this.state.portfolios//renewing the whole list of portfolios
ports[this.state.portfolios.map((portfolio,index)=>{if (portfolio.id==this.state.stockbeingaddedto) return index}).filter((p)=>typeof p !== 'undefined')[0]]=theportfoliotoedit
	this.setState(state => ({
    	    stockbeingaddedto: false,
	    portfolios:[...ports]
	  }));localStorage.setItem('state', JSON.stringify(this.state));






//new stock added? update all stocks data
