	{

	  var currentobject=data[i]


	  currentobject.info=currentobject.info.map((oneobject)=>{
	delete Object.assign(oneobject, {[currentobject.Name]: oneobject["price"] })["price"];
	return oneobject})
	  currentobject=currentobject.info
	  data[i]=currentobject
/**/
	}
//console.log("data1")
/*console.log("data1")
console.log(data)*/


const formatteddata=data.reduce((a1,a2)=>{//"JavaScript merging objects by id"

for (let t=0; t<a1.length; t++)
    {var date=a1[t].date
            for (let tt=0; tt<a2.length; tt++)
                {var date2=a2[tt].date
                    if(date==date2)
                      {a1[t]={...a1[t],...a2[tt]}}
                }
    }
return a1

})



